*
    margin: 0
    padding: 0
    box-sizing: border-box
    font-family: Avenir

@font-face
    font-family: 'Avenir'
    src: url('AvenirLTStd-Medium.ttf') format('truetype')
@font-face
    font-family: 'Avenir-55'
    src: url('AvenirLTStd-Roman.ttf') format('truetype')
@font-face
    font-family: 'Avenir-45'
    src: url('AvenirLTStd-Book.ttf') format('truetype')

.body
    background-image: url(bg-image.png)
    background-repeat: repeat-y
    overflow-x: hidden
    background-size: 100%
.name-container
    margin: 0 auto
    margin-top: 19vh
    height: fit-content
    width: 65vw
    font-size: 80px
    text-align: center
    allign-items: center
    width: 100%

.download-resume-btn
    z-index: -2
    font-family: Avenir-55
    background-color: white
    font-size: 23px
    line-height: 36px
    display: flex
    align-items: center
    text-align: center
    letter-spacing: -0.025em
    text-transform: uppercase
    justify-content: center
    width: 350px
    height: 70px
    border: 1px solid black
    margin: 50px auto
    cursor: pointer
    background-image: url(button-bg.png)
    background-repeat: no-repeat
    background-size: 150%
    background-position: 500%
    transition: 0.4s ease-out
    overflow: hidden
    color: #000000 
    text-decoration: none


.download-resume-btn:hover
    color: white
    background-position: 10%
.zain
    margin: none
    justify-content: center
    align-items: center
    height: 105px
    text-transform: uppercase
    overflow: hidden
    display: inline-block
.bodymouse
    margin-bottom: 34vh
    display: flex
    justify-content: center
    position: absolute
    width: 23px
    height: 34px
    border: 2px solid #000000
    box-sizing: border-box
    border-radius: 37px
.scroll
    position: relative
    width: 2.34px
    height: 6.75px
    background: #000000
    border-radius: 5px
    margin-top: 5px
.mouse
    display: flex
    justify-content: center
    allign-item: center
    margin-top: 27vh
.zain span
    marigin-bottom: 7px
    animation: animate 10s ease infinite
    position: relative
@keyframes animate
    0%,100%
        top: 0px
    20%
        top: -100px
    40%
        top: -200px
    60%
        top: -300px
    80%
        top: -400px
.scroll
    animation: animatemouse 2s ease-in-out infinite
    position: relative

@keyframes animatemouse
    0%
        top: 0px
    50%
        top: 5px
    100%
        top: 0px
// <!-- ------------------------------------------About----------------------------------------------------------------------------------------->
.about-all
    margin-top: 30vh
.about-container
    text-align: center
    margin: 0 auto
.info
    margin-top: 12vh
    margin-left: 25vw
    text-align: left
.about
    font-family: Avenir-55
    line-height: 100px
    font-size: 50px
    letter-spacing: -0.025em
    text-decoration-line: underline
.whos
    font-family: Avenir-55
    font-size: 30px
    letter-spacing: 0.25em
    text-transform: uppercase
    color: #6C6C6C
.desiner
    font-size: 30px
    line-height: 130px
    letter-spacing: -0.025em
    text-transform: uppercase
.description
    font-size: 25px
    line-height: 58px
    letter-spacing: -0.025em
    color: #6C6C6C
    width: 900px
    // <!-- ------------------------------------------Project----------------------------------------------------------------------------------------->
.main-project
    display: flex
    justify-content: center
.project-container
    text-align: center
.project
    font-family: Avenir-55
    line-height: 100px
    margin-bottom: 10vh
    margin-top: 30vh
    font-size: 60px
    letter-spacing: -0.025em
    text-decoration-line: underline
.cardsmain
    width: 1400px
    margin-top: 8vh
    display: flex
    justify-content: space-evenly
.card1
    width: 300px
    height: 400px
    background-image: url(images/RL.png)
    text-align: left
    transition: 0.2s all

.card1:hover
    z-index: 1
    box-shadow: 0px 6px 11px rgba(0, 0, 0, 0.3)
    transform: scale(1.05)
.card2
    width: 300px
    height: 400px
    background-image: url(images/Fri.png)
    text-align: left
    transition: 0.2s all

.card2:hover
    z-index: 1
    box-shadow: 0px 6px 11px rgba(0, 0, 0, 0.3)
    transform: scale(1.05)
.card3
    width: 300px
    height: 400px
    background-image: url(images/Zel.png)
    text-align: left
    transition: 0.2s all

.card3:hover
    z-index: 1
    box-shadow: 0px 6px 11px rgba(0, 0, 0, 0.3)
    transform: scale(1.05)

.text
    z-index: 6
    font-family: 'Poppins', sans-serif
    font-style: normal
    font-weight: normal
    font-size: 12px

.year
    font-size: 12px
    line-height: 251.4%
    letter-spacing: 0.3em
    margin-top: 36px
    margin-bottom: 4px
    margin-left: 24px

.project-discription
    font-size: 16px
    line-height: 20px
    width: 200px
    margin-left: 24px




// <!-- ------------------------------------------Contact----------------------------------------------------------------------------------------->
.Contactme
    padding-top: 8vh
    text-align: center
    font-size: 50px 
    color: white
    text-decoration: underline
.contact-page
    width: 100%
    height: 500px
    margin-top: 20vh
    margin-bottom: none
    background-repeat: no-repeat
    background-color: #252934
.accounts
    display: flex
    width: 700px
    margin: 0 auto
    margin-top: 10vh
    img
        transition: transform 2s ease-in-out
.statement
    margin-top: 5%
    font-size: 30px
    color: white
    text-align: center

.facebook ,.insta ,.git ,.gmail
    display: flex
    align-items: center
    justify-content: center
    background-color: #354351
    margin: 0 auto
    width: 75px
    height: 75px
    border-radius: 10px
.gmail:hover
    background-color: #2b3846
.insta:hover
    background-color: #2b3846
.facebook:hover
    background-color: #2b3846
.git:hover
    background-color: #2b3846
.creds
    text-align: center
    color: white
    margin-top: 3%
    opacity: 40%
